@import "mixin";

@import "header";

@include site-h;


body {
	min-width: 1120px;
	margin: 0; }
.bunner {
	border-bottom: 2px solid lighten($text, 30%); }

.st-container {
	.logo {
		div {
			color: $text; } }
	nav {
		a {
			color: $text; } } }

.writing {
	width: $width;
	max-width: 95%;
	margin: auto;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	font-family: "FuturaPT";
	color: $text;
	.product {
		width: 30%;
		.img {
			width: 100%;
			height: 200px;
			box-sizing: border-box;
			margin-bottom: 25px; } }
	.product:nth-child(3n+1) .img {
		background: url("../image/Picture-10.png");
		background-size: cover; }
	.product:nth-child(-3n+5) .img {
		background: url("../image/Picture-11.png");
		background-size: cover; }
	.product:nth-child(-3n+6) .img {
		background: url("../image/Picture-12.png");
		background-size: cover; }

	a {
		text-decoration: none;
		color: $green;
		font-size: 16px; }
	a:hover {
		color: darken($green, 10%); }
	.caption {
		line-height: 22px;
		font-size: 24px;
		margin-top: 25px;
		margin-bottom: 15px; }
	.text {
		font-size: 18px;
		font-family: "FuturaPTB";
		text-align: justify;
		line-height: 18px;
		margin-bottom: 60px; } }




@import "footer";

